.input-field{
    border: none;
    outline: none;
    background: var(--black-color);
    color: var(--white-color);
    border-radius: 0.3rem;
    padding: 0rem 0.3rem;
    font-family: 'Poppins', sans-serif;
    margin: 0.5rem 0rem;
}

.input-field:hover{
    border: 0.13rem solid var(--me-blue);
}

.input-field:focus{
    border: 0.13rem solid var(--primary-color);
}

.lable{
    font-size: 1.3rem;
    color: var(--white-color);
    font-weight: 500;
    padding: 0.3rem 0;
    font-family: 'Source Code Pro', sans-serif;
}

textarea{
    resize: none;
    padding: 1rem;
}