@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&family=Roboto:wght@400;500&family=Source+Code+Pro:wght@300;400;500;600&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
}

:root{
    --primary-color: #ff3e6c;
    --secondary-color: #575fcf;
    --me-blue: #0079fc;
    --white-color: #fff;
    --black-color: #161616;
    --dark-color: #313131;
    --me-dark-color: #232323;
    --light-color: #a4acc4;
    --font-dark-color: #313131;
    --font-dark-color-high: #151515;
    --border-color: #ebebeb;
    --sidebar-dark-color: #191d2b;
    --sidebar-dark-color-high: #242a3e;
    --scrollbar-bg-color: #ebebeb;
    --scrollbar-thump-color: #cdcdcd;
    --scrollbar-track-color: #fff;
}

::-webkit-scrollbar{
    width: 8px;
    background-color: var(--scrollbar-bg-color);
}

::-webkit-scrollbar-thumb{
    border-radius: 10px;
    background-color: var(--scrollbar-thump-color);
}

::-webkit-scrollbar-track{
    border-radius: 10px;
    background-color: var(--scrollbar-track-color);
}


body{
    font-family: 'Poppins', sans-serif;
    background-color: var(--me-dark-color);
}

/* Theme */
.bg-black{
    background-color: var(--black-color);
}

.container{
    width: 95%;
    margin: 1rem auto;
    overflow: hidden;
}

.card{
    min-width: 100px;
    min-height: 100px;
    background: var(--sidebar-dark-color-high);
    font-family: 'Source Code Pro', sans-serif;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    margin: 0.4rem;
    box-shadow: 3px 7px 15px rgba(0, 0, 0, 0.5);
}

.card:hover{
    background-color: var(--sidebar-dark-color);
}

.bolder{
    font-weight: bolder;
}

/* Heading */
.s-heading{
    font-size: 1.1rem;
    font-weight: 500;
    color: var(--white-color);
    padding: 0.1rem 0rem;
    transition: all 0.4s ease-in-out;
    font-family: 'Source Code Pro', sans-serif;
}
/* Levels */
.level{
    font-size: 0.8rem;
    color: var(--white-color);
    font-family: 'Source Code Pro', sans-serif;
    padding: 0rem 0rem 0rem 0.2rem;
}

.icon-level{
    font-size: 0.8rem;
    color: var(--white-color);
    font-family: 'Source Code Pro', sans-serif;
    transition: all 0.4s ease-in-out;
    padding: 0rem 0rem 0rem 0.2rem;
}

.level-a{
    color: #00ff00;
}

.level-b{
    color: #00ffff;
}

.level-c{
    color: #ffff00;
}

.icon-level{
    opacity: 0.5;
}

.level:hover > .icon-level{
    opacity: 1;
    font-size: 1.1rem;
}

/* Flex */
.flex{
    display: flex;
}

.flex-row{
    flex-direction: row;
}

.flex-column{
    flex-direction: column;
}

.flex-wrap{
    flex-wrap: wrap;
}

.flex-nowrap{
    flex-wrap: nowrap;
}

.flex-sb{
    justify-content: space-between;
}

.flex-sa{
    justify-content: space-around;
}

.flex-center{
    justify-content: center;
}

.flex-end{
    justify-content: flex-end;
}

.flex-ai-center{
    align-items: center;
}

.grid{
    display: grid;
}

.g-t-2{
    grid-template-columns: 1fr 1fr 1fr;
}

.g-t-2-me{
    grid-template-columns: 1fr 1fr;
}

/* gap */
.gap-1-rm{
    gap: 1rem;
}

/* Width */
.w-100{
    width: 100%;
}

.w-90{
    width: 90%;
}

.w-80{
    width: 80%;
}

.w-70{
    width: 70%;
}

.w-60{
    width: 60%;
}

.w-50{
    width: 50%;
}

.w-40{
    width: 40%;
}

.w-30{
    width: 30%;
}

/* hieght */
.h-50px{
    height: 50px;
}

.h-100px{
    height: 100px;
}

.h-200px{
    height: 200px;
}

.h-300px{
    height: 300px;
}

/* Text */
.text-d-n{
    text-decoration: none;
}

.text-center{
    text-align: center;
}

.text-justify{
    text-align: justify;
}

.user-select-none{
    user-select: none;
}

.user-select-yes{
    user-select: text;
}

.block{
    display: block;
}

/* Floats */
.float-right{
    float: right;
    color: #bb1c00;
}

/* Margin */
.m-auto{
    margin: auto;
}

.m-default-bottom{
    margin-bottom: 1rem;
}

.m-default-top{
    margin-top: 1rem;
}

/* Display Hidden */
.hide{
    display: none;
}