.touchMe{
    margin: 1rem;
    padding: 2rem 1rem;
    background: var(--font-dark-color);
}

.p-title{
    font-size: 1.5rem;
    font-weight: 500;
    line-height: 1.5;
    color: var(--me-blue);
    transition: all 0.4s ease-in-out;
    font-family: 'Source Code Pro', sans-serif;
}

.p-title:hover{
    color: var(--secondary-color);
    letter-spacing: 0.5px;
}

.fancyText{
    font-size: 1rem;
    color: var(--primary-color);
    padding: 0.3rem 0;
    transition: all 0.4s ease-in-out;
    font-family: 'Source Code Pro', sans-serif;
}

.sub-p{
    font-size: 0.9rem;
    color: var(--white-color);
    padding: 0.3rem 0;
    transition: all 0.4s ease-in-out;
    font-family: 'Source Code Pro', sans-serif;
}

.linkIt{
    font-size: 0.9rem;
    color: yellow;
    padding: 0.3rem 0;
    transition: all 0.4s ease-in-out;
    font-family: 'Source Code Pro', sans-serif;
}

.icon-p{
    color: var(--white-color);
    font-size: 1.3rem;
}
