.btn-intro{
    border: none;
    outline: none;
    background: var(--primary-color);
    color: var(--white-color);
    padding: 0.5rem 1rem;
    border-radius: 0.5rem;
    font-size: 1.2rem;
    cursor: pointer;
    font-family: 'Source Code Pro', sans-serif;
}

.btn-intro:hover{
    animation: animateBtnIntro 1s ease-in-out forwards;
    font-style: italic;
    background: linear-gradient(130deg, var(--me-blue) 0%, var(--secondary-color) 100%);
}

.iconIt{
    font-size: 1.2rem;
    margin-left: 1rem;
}

@keyframes animateBtnIntro{
    0%{
        transform: translateX(0);
    }
    50%{
        transform: translateX(3px);
    }
    100%{
        transform: translateX(0);
    }
}

@media screen and (max-width: 400px){
    .btns{
        flex-direction: column;
    }
}