#Navigation{
    padding: 0.8rem 1.5rem;
    user-select: none;
}

#lowerNav{
    position: relative;
}

.navButton{
    position: relative;
    justify-content: center;
    padding: 0.8rem 1.5rem;
    border-radius: 0.5rem;
    margin: 0 0.5rem;
    font-weight: 400;
    color: var(--white-color);
    font-family: 'Roboto', sans-serif;
    cursor: pointer;
    transition: all 0.3s ease;
}

.navButton::before{
    content: "";
    position: absolute;
    width: 0%;
    height: 3px;
    border-radius: 5px;
    bottom: 0;
    background-color: var(--primary-color);
    opacity: 0;
    border-radius: 0.5rem;
    transition: all 0.3s ease;
}

.navButton:hover{
    color: var(--white-color);
    letter-spacing: 0.2px;
    font-weight: 500;
}

.navButton:hover::before{
    width: 100%;
    opacity: 1;
}

.myName{
    font-size: 1.5rem;
    font-weight: 500;
    color: var(--white-color);
    margin: 0 0.5rem;
    font-family: 'Source Code Pro', monospace;
}


#projects-drop-down{
    position: relative;
}

#downIt{
    transition: all 0.2s ease-in-out;
    color: var(--white-color);
}


#projects-drop-menu{
    position: absolute;
    top: 100%;
    width: 200px;
    display: none;
    background: var(--sidebar-dark-color);
    z-index: 100;
}

#projects-drop-menu:hover{
    background: var(--sidebar-dark-color-high);
}


#projects-drop-down:hover > #projects-drop-menu{
    display: block;
}

#projects-drop-down:hover > #downIt{
    rotate: 180deg;
}

#hamberger{
    display: none;
    position: absolute;
    left: 95%;
}

.hamberger-line{
    color: var(--white-color);
    font-size: 1.2rem;
}

.show-links{
    display: flex;
}

/* Responsive Navigation */
@media screen and (max-width: 1060px){
    #Navigation #logoName{
        display: none;
    }
}

@media screen and (max-width: 916px){
    #Navigation #logoName{
        display: block;
    }

    /* .navLinks{
        display: none;
    } */

    .navButton{
        justify-content: start;
    }


    .navLinks-child-now{
        transition-delay: 0.1s;
        padding-right: 2rem;
        position: absolute;
        top: 0;
        left: -25px;
        justify-content: left;
        background: var(--sidebar-dark-color-high);
        width: 70%;
        display: flex;
        z-index: 1000;
        flex-direction: column;
    }

    #projects-drop-menu{
        top: 100%;
        left: 50%;
    }

    #projects-drop-down:hover > #downIt{
        rotate: 270deg;
        margin-left: 1rem;
    }
    

    .nav-child{
        display: none;
    }

    #hamberger{
        display: block;
    }
}