.iconFT{
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--primary-color);
    font-size: 1.5rem;
    transition: all 0.3s ease;
}

.footLinks a{
    padding: 0.5rem 1rem;
    gap: 5px;
    background: var(--dark-color);
    color: var(--white-color);
    border-radius: 0.5rem;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    transition: all 0.3s ease;
    min-width: 100px;
    max-width: 140px;
}

.footLinks a:hover {
    background: var(--sidebar-dark-color);
    color: var(--white-color);
}

.footLinks a:hover > .iconFT{
    color: var(--me-blue);

}

.p-heading{
    font-size: 1.3rem;
    color: var(--white-color);
    padding: 1rem 0;
    font-weight: 400;
    font-family: 'Source Code Pro', monospace;
}

.foot-p{
    color: var(--white-color);
    font-size: 1rem;
    padding: 0.5rem 0;
    font-family: 'Source Code Pro', monospace;
}

/* Responsive Footer */
@media screen and (max-width: 750px){
    .footLinks{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 2rem;
        flex-wrap: wrap;
    }

    .lowerFoot{
        padding: 0rem 2rem;
    }

    .footLinks a{
        width: 100%;
    }
}
